.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  position: relative;
  width: 90%;
  max-width: 1000px; /* Adjusted max-width for vertical layout */
  max-height: 85vh;   /* Adjusted max-height */
  display: flex;
  flex-direction: column;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 15px;
  background: none;
  border: none;
  font-size: 2.5rem;
  cursor: pointer;
  color: #333;
}

.modal-image-strip {
  display: flex;
  flex-direction: column; /* Stack images vertically */
  align-items: center;    /* Center images horizontally */
  overflow-y: auto;       /* Enable vertical scrolling */
  gap: 20px;              /* Space between images */
  padding: 20px;
  margin-top: 30px; /* Add margin to avoid overlap with close button */
}

.modal-image-strip img {
  width: 100%;
  height: auto;           /* Maintain aspect ratio */
  object-fit: contain;
  border-radius: 4px;
}