.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
  margin-bottom: 30px;
}

.main-category-section {
  margin-bottom: 50px;
}

.main-category-title {
  font-size: 2.5rem;
  text-transform: uppercase;
  border-bottom: 3px solid #eee;
  padding-bottom: 10px;
  margin-bottom: 30px;
  text-align: center;
}

.sub-category-section {
  margin-bottom: 40px;
  padding: 0 20px;
}

.sub-category-section h2 {
  font-size: 1.8rem;
  text-align: left;
  margin-bottom: 20px;
  padding-left: 10px;
  border-left: 5px solid #282c34;
}

.model-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

.model-card {
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
  background-color: #fff;
}

.model-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0,0,0,0.1);
}

.representative-image {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

.no-image-placeholder {
  width: 100%;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f0f0f0;
  color: #aaa;
}

.model-card h3 {
  padding: 15px;
  margin: 0;
  background-color: #f9f9f9;
  font-size: 1rem;
  text-align: center;
}